<script>
export default {
  data() {
    return {
      settings: {
        receiveNotifications: false,
        darkMode: false,
        font: 'Arial'
      }
    };
  },
  mounted() {
    const saved = localStorage.getItem('userSettings');
    if (saved) {
      this.settings = JSON.parse(saved);
      this.applyTheme();
      this.applyFont();
    }
  },
  methods: {
    saveSettings() {
      localStorage.setItem('userSettings', JSON.stringify(this.settings));
    },
    applyTheme() {
      document.body.classList.toggle('dark-mode', this.settings.darkMode);
      this.saveSettings();
    },
    applyFont() {
      document.body.style.fontFamily = this.settings.font;
      this.saveSettings();
    },
    goToHome() { this.$router.push('/dashboard'); },
    goToCourses() { this.$router.push('/courses'); },
    goToStudents() { this.$router.push('/students'); },
    goToPayments() { this.$router.push('/payments'); },
    goToArchive() { this.$router.push('/archive'); },
    goToReports() { this.$router.push('/reports'); },
    goToSettings() { this.$router.push('/settings'); },
    logout() { this.$router.push('/'); }
  }
}
</script>
