<template>
  <div class="dashboard">
    <div class="sidebar">
      <h2><span class="accent">|</span> CRUD OPERATIONS</h2>
      <img
        src="data:image/jpeg;base64,"
        alt="Profile"
        class="avatar"
      />
      <h3>Karthi Madesh</h3>
      <p class="role">Admin</p>
      <ul>
        <li :class="{ active: currentRoute === 'home' }" @click="goToHome">üè† Home</li>
        <li :class="{ active: currentRoute === 'courses' }" @click="goToCourses">üìò Course</li>
        <li :class="{ active: currentRoute === 'students' }" @click="goToStudents">üéì Students</li>
        <li :class="{ active: currentRoute === 'payments' }" @click="goToPayments">üí∞ Payment</li>
        <li :class="{ active: currentRoute === 'archive' }" @click="goToArchive">üóÇÔ∏è Archive</li>
        <li :class="{ active: currentRoute === 'reports' }" @click="goToReports">üìä Report</li>
        <li :class="{ active: currentRoute === 'settings' }" @click="goToSettings">‚öôÔ∏è Settings</li>
      </ul>
      <button class="logout" @click="logout">Logout</button>
    </div>

    <div class="main">
      <div class="cards">
        <div class="card lightblue">
          <div class="icon">
            <img src="./Vector.png" alt="Students Icon" />
          </div>
          <p class="label">Students</p>
          <p class="value">{{ students }}</p>
        </div>
        <div class="card pink">
          <div class="icon">
            <img src="./bookmark 1.png" alt="Course Icon" />
          </div>
          <p class="label">Course</p>
          <p class="value">{{ courses }}</p>
        </div>
        <div class="card lightyellow">
          <div class="icon">
            <img src="./usd-square 1.png" alt="Money Icon" />
          </div>
          <p class="label">Payments</p>
          <p class="value">{{ formattedPayments }}</p>
        </div>
        <div class="card orange">
          <div class="icon">
            <img src="./Vector (1).png" alt="Users Icon" />
          </div>
          <p class="label">Users</p>
          <p class="value">{{ users }}</p>
        </div>
      </div>

      <div class="charts-section">
        <div class="chart-wrapper">
          <StatsChart :students="students" :courses="courses" :users="users" />
        </div>
        <div class="pie-wrapper">
          <PaymentsChart :payments="payments" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import StatsChart from './StatsChart.vue';
import PaymentsChart from './PaymentsChart.vue';

export default {
  name: 'DashboardView',
  components: {
    StatsChart,
    PaymentsChart,
  },
  data() {
    return {
      currentRoute: 'home',
      students: 0,
      courses: 0,
      payments: 0,
      users: 0,
    };
  },
  computed: {
    formattedPayments() {
      // –§–æ—Ä–º–∞—Ç–∏—Ä—É–µ–º –ø–ª–∞—Ç–µ–∂–∏ –≤ –∏–Ω–¥–∏–π—Å–∫–æ–º —Ñ–æ—Ä–º–∞—Ç–µ —Å —Å–∏–º–≤–æ–ª–æ–º INR
      return this.payments.toLocaleString('en-IN', {
        style: 'currency',
        currency: 'INR',
        minimumFractionDigits: 0,
      });
    },
  },
  methods: {
    goToHome() {
      this.currentRoute = 'home';
      this.$router.push('/dashboard');
    },
    goToCourses() {
      this.currentRoute = 'courses';
      this.$router.push('/courses');
    },
    goToStudents() {
      this.currentRoute = 'students';
      this.$router.push('/students');
    },
    goToPayments() {
      this.currentRoute = 'payments';
      this.$router.push('/payments');
    },
    goToArchive() {
      this.currentRoute = 'archive';
      this.$router.push('/archive');
    },
    goToReports() {
      this.currentRoute = 'reports';
      this.$router.push('/reports');
    },
    goToSettings() {
      this.currentRoute = 'settings';
      this.$router.push('/settings');
    },
    logout() {
      this.$router.push('/');
    },
    animateValue(property, start, end, duration) {
      let startTimestamp = null;
      const step = (timestamp) => {
        if (!startTimestamp) startTimestamp = timestamp;
        const progress = Math.min((timestamp - startTimestamp) / duration, 1);
        this[property] = Math.floor(progress * (end - start) + start);
        if (progress < 1) {
          window.requestAnimationFrame(step);
        }
      };
      window.requestAnimationFrame(step);
    },
  },
  mounted() {
    this.animateValue('students', 0, 243, 2000);
    this.animateValue('courses', 0, 13, 2000);
    this.animateValue('payments', 0, 556000, 2000);
    this.animateValue('users', 0, 3, 2000);
  },
};
</script>

<style scoped>
body,
