<template>
  <div class="container">
    <div class="sidebar">
      <h2><span class="accent">|</span> CRUD OPERATIONS</h2>
      <img src="data:image/jpeg;base64," alt="Profile" class="avatar" />
      <h3>Karthi Madesh</h3>
      <p class="role">Admin</p>
      <ul>
        <li @click="goToDashboard">üè† Home</li>
        <li @click="goToCourses">üìò Course</li>
        <li class="active" @click="goToStudents">üéì Students</li>
        <li @click="goToPayments">üí∞ Payment</li>
        <li @click="goToArchive">üóÇÔ∏è Archive</li>
        <li @click="goToReports">üìä Report</li>
        <li @click="goToSettings">‚öôÔ∏è Settings</li>
      </ul>
      <button class="logout" @click="logout">Logout</button>
    </div>

    <div class="main">
      <div class="header">
        <h2>Students List</h2>
        <button class="add-new-student-button" @click="addStudent">Add New Student</button>
      </div>

      <table>
        <thead>
          <tr>
            <th>Avatar & Name</th>
            <th>Email</th>
            <th>Phone</th>
            <th>Enroll Number</th>
            <th>Date of Admission</th>
            <th>Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="student in students" :key="student.id">
            <td>
              <div class="student-info">
                <img :src="student.avatar || avatarImage" class="avatar-small" />
                <div class="name-upload">
                  <div v-if="student.editing">
                    <input
                      type="file"
                      :id="'file-' + student.id"
                      class="file-input"
                      @change="onImageChange($event, student)"
                      accept="image/*"
                    />
                    <label :for="'file-' + student.id" class="upload-button">üìÅ Upload</label>
                    <input v-model="student.name" class="nameSt" />
                  </div>
                  <div v-else>
                    <span>{{ student.name }}</span>
                  </div>
                </div>
              </div>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.email" />
              <span v-else>{{ student.email }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.phone" />
              <span v-else>{{ student.phone }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.enrollNumber" />
              <span v-else>{{ student.enrollNumber }}</span>
            </td>
            <td>
              <input v-if="student.editing" v-model="student.date" type="date" />
              <span v-else>{{ student.date }}</span>
            </td>
            <td>
              <select v-if="student.editing" v-model="student.status">
                <option value="Active">Active</option>
                <option value="Graduate">Graduate</option>
                <option value="Frozen">Frozen</option>
                <option value="Churn">Churn</option>
              </select>
              <span v-else>{{ student.status }}</span>
            </td>
            <td>
              <button
                v-if="student.editing"
                @click="saveStudent(student.id)"
                class="action-button save-button"
              >üíæ</button>
              <button
                v-else
                @click="editStudent(student.id)"
                class="action-button edit-button"
              >‚úèÔ∏è</button>
              <button
                @click="deleteStudent(student.id)"
                class="action-button delete-button"
              >üóëÔ∏è</button>
            </td>
          </tr>
          <tr v-if="students.length === 0">
            <td colspan="7" style="text-align: center;">No students found.</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'StudentsView',
  data() {
    return {
      avatarImage: 'https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcS8oghbsuzggpkknQSSU-Ch_xep_9v3m6EeBQ&s',
      students: [],
    };
  },
  mounted() {
    this.loadStudentsFromLocalStorage();
  },
  methods: {
    goToDashboard() { this.$router.push('/dashboard'); },
    goToCourses() { this.$router.push('/courses'); },
    goToPayments() { this.$router.push('/payments'); },
    goToStudents() { this.$router.push('/students'); },
    goToReports() { this.$router.push('/reports'); },
    goToSettings() { this.$router.push('/settings'); },
    goToArchive() { this.$router.push('/archive'); },
    logout() { this.$router.push('/'); },

    addStudent() {
      const newId = this.students.length > 0
        ? Math.max(...this.students.map(s => s.id)) + 1
        : 1;
      const newStudent = {
        id: newId,
        name: "New Student",
        email: "new.student@example.com",
        phone: "111-222-3333",
        enrollNumber: "ENR" + String(newId).padStart(3, '0'),
        date: new Date().toISOString().slice(0, 10),
        avatar: this.avatarImage,
        editing: true,
        status: "Active",
      };
      this.students.push(newStudent);
      this.saveStudentsToLocalStorage();
    },
    editStudent(id) {
      const student = this.students.find(s => s.id === id);
      if (student) student.editing = true;
    },
    saveStudent(id) {
      const student = this.students.find(s => s.id === id);
      if (student) {
        if (student.status === "Churn") {
          const archive = JSON.parse(localStorage.getItem('archive') || '[]');
          archive.push({ ...student, editing: false });
          localStorage.setItem('archive', JSON.stringify(archive));
          this.students = this.students.filter(s => s.id !== id);
        } else {
          student.editing = false;
        }
        this.saveStudentsToLocalStorage();
      }
    },
    deleteStudent(id) {
      if (confirm("Are you sure you want to delete this student?")) {
        this.students = this.students.filter(s => s.id !== id);
        this.saveStudentsToLocalStorage();
      }
    },
    loadStudentsFromLocalStorage() {
      const data = localStorage.getItem('students');
      if (data) {
        this.students = JSON.parse(data);
      } else {
        this.students = [];
        this.saveStudentsToLocalStorage();
      }
    },
    saveStudentsToLocalStorage() {
      localStorage.setItem('students', JSON.stringify(this.students));
    },
    onImageChange(event, student) {
      const file = event.target.files[0];
      if (file && file.type.startsWith("image/")) {
        const reader = new FileReader();
        reader.onload = e => {
          student.avatar = e.target.result;
        };
        reader.readAsDataURL(file);
      }
    },
  },
};
</script>

