<template>
  <div class="container">
    <div class="sidebar">
      <h2><span class="accent">|</span> CRUD OPERATIONS</h2>
      <img
        :src="avatarImage"
        alt="Profile"
        class="avatar"
      />
      <h3>Karthi Madesh</h3>
      <p class="role">Admin</p>
      <ul>
        <li @click="goToDashboard">üè† Home</li>
        <li @click="goToCourses">üìò Course</li>
        <li @click="goToStudents">üéì Students</li>
        <li @click="goToPayments">üí∞ Payment</li>
        <li class="active" @click="goToArchive">üóÇÔ∏è Archive</li>
        <li @click="goToReports">üìä Report</li>
        <li @click="goToSettings">‚öôÔ∏è Settings</li>
      </ul>
      <button class="logout" @click="logout">Logout</button>
    </div>

    <div class="main">
      <h2>Archived Students</h2>
      <table>
        <thead>
          <tr>
            <th>Name</th>
            <th>Email</th>
            <th>Phone</th>
            <th>Enroll Number</th>
            <th>Date of Admission</th>
            <th>Status</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="student in archive" :key="student.id">
            <td>
              <img :src="student.avatar || avatarImage" class="avatar-small" />
              {{ student.name }}
            </td>
            <td>{{ student.email }}</td>
            <td>{{ student.phone }}</td>
            <td>{{ student.enrollNumber }}</td>
            <td>{{ student.date }}</td>
            <td>{{ student.status }}</td>
            <td>
              <button @click="restoreStudent(student.id)" class="action-button">‚Ü©Ô∏è</button>
              <button @click="deleteStudent(student.id)" class="action-button">üóëÔ∏è</button>
            </td>
          </tr>
          <tr v-if="archive.length === 0">
            <td colspan="7" style="text-align: center;">No archived students.</td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ArchiveView',
  data() {
    return {
      archive: [],
      avatarImage: "data:image/jpeg;base64,",
    };
  },
  mounted() {
    this.loadArchive();
  },
  methods: {
    goToDashboard() { this.$router.push('/dashboard'); },
    goToCourses() { this.$router.push('/courses'); },
    goToPayments() { this.$router.push('/payments'); },
    goToStudents() { this.$router.push('/students'); },
    goToReports() { this.$router.push('/reports'); },
    goToSettings() { this.$router.push('/settings'); },
    goToArchive() { this.$router.push('/archive'); },
    logout() { this.$router.push('/'); },

    loadArchive() {
      const data = localStorage.getItem('archive');
      if (data) {
        this.archive = JSON.parse(data);
      }
    },
    saveArchive() {
      localStorage.setItem('archive', JSON.stringify(this.archive));
    },
    restoreStudent(id) {
      const student = this.archive.find(s => s.id === id);
      if (student) {
        const students = JSON.parse(localStorage.getItem('students') || '[]');
        students.push({ ...student, status: "Active", editing: false });
        localStorage.setItem('students', JSON.stringify(students));

        this.archive = this.archive.filter(s => s.id !== id);
        this.saveArchive();
      }
    },
    deleteStudent(id) {
      if (confirm("Are you sure you want to delete this archived student?")) {
        this.archive = this.archive.filter(s => s.id !== id);
        this.saveArchive();
      }
    },
  },
};
</script>

<style scoped>
body,
html {
  margin: 0;
  font-family: Arial, sans-serif;
  height: 100vh;
}
.container {
  display: flex;
}

.sidebar {
  background: #f5f5f5;
  width: 200px;
  padding: 20px;
  text-align: center;
}

.avatar-small {
  width: 30px;
  height: 30px;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 8px;
  vertical-align: middle;
}

.accent {
  color: #facc15;
  margin-right: 5px;
}

.avatar {
  border-radius: 50%;
  width: 100px;
  margin: 20px 0 10px;
}

.role {
  color: orange;
  font-size: 14px;
  margin-bottom: 20px;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  padding: 10px;
  text-align: left;
  cursor: pointer;
}

li.active,
li:hover {
  background-color: #facc15;
  color: white;
  border-radius: 5px;
}

.logout {
  margin-top: 20px;
  padding: 10px;
  width: 100%;
  background: none;
  border: 1px solid #ccc;
  cursor: pointer;
}

.main {
  flex: 1;
  padding: 20px;
  background: #fff;
}

table {
  width: 100%;
  border-collapse: collapse;
  background: white;
  margin-top: 20px;
}

th,
td {
  padding: 10px;
  text-align: left;
  border-bottom: 1px solid #ddd;
}

th {
  background-color: #facc15;
  color: white;
  font-weight: 600;
}

.action-button {
  border: none;
  background: none;
  cursor: pointer;
  font-size: 18px;
  margin-right: 5px;
}

.action-button:hover {
  color: #facc15;
}
</style>
